@font-face {
  font-family: 'Proxima Nova';
  src: url('../../assets/ProximaNova-Regular.woff2');
  font-weight: 400;
  font-style: normal;
  font-display: fallback;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('../../assets/ProximaNova-Bold.woff2');
  font-weight: 700;
  font-style: normal;
  font-display: fallback;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('../../assets/ProximaNova-RegularIt.woff2');
  font-weight: 400;
  font-style: italic;
  font-display: fallback;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('../../assets/ProximaNova-BoldIt.woff2');
  font-weight: 700;
  font-style: italic;
  font-display: fallback;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-head {
  grid-area: header;

}

.content {
  grid-area: content;
  overflow: auto;
}

.breadcrumb {
  grid-area: breadcrumb;

}

.main-nav {
  grid-area: nav;
  overflow: auto;
}

.main-footer {
  grid-area: footer;

}

.wrapper {
  display: grid;
  height: 100vh;
  grid-template-areas:
    "header header "
    "nav breadcrumb"
    "nav content"
    "footer footer";
  grid-template-columns: auto 6fr;
  grid-template-rows: 3em 40px calc(100vh - 6em - 40px) 3em;
}